<template>
  <div class="layout">
    <div v-for="gallery in state.gallery" :key="gallery.name" class="gallery">
      <h2 class="font-weight-normal">{{ gallery.name }}</h2>
      <div class="img-zone">
        <div
          v-for="image in gallery.images"
          :key="image"
          class="gallery-img"
          :style="{ backgroundImage: `url(${image})` }"
          @click="state.selectedImage = { title: gallery.name, image: image }"
        >
          <div class="gallery-img-hover">
            <i class="mdi mdi-magnify-plus-outline mdi-lg"></i>
          </div>
        </div>
      </div>
    </div>
  </div>

  <Dialog v-if="state.selectedImage" @close="state.selectedImage = null">
    <template v-slot:title>{{ state.selectedImage.title }}</template>
    <img :src="state.selectedImage.image" :alt="state.selectedImage.title" />
  </Dialog>
</template>

<style scoped>
.img-zone {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.gallery-img {
  position: relative;
  height: 300px;
  background-size: cover;
  background-repeat: no-repeat;
  cursor: pointer;
}

.gallery-img-hover {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.gallery-img:hover .gallery-img-hover {
  visibility: visible;
  opacity: 1;
}

img {
  width: 100%;
  height: auto;
}

@media screen and (max-width: 800px) {
  .img-zone {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 430px) {
  .img-zone {
    grid-template-columns: 1fr;
  }
  .gallery-img {
    height: 200px;
  }
}
</style>

<script>
import Dialog from "@/components/Dialog.vue";
import { reactive } from "vue";

export default {
  components: { Dialog },
  setup() {
    const state = reactive({
      selectedImage: null,
      gallery: [
        {
          name: "Stupid Hackathon Thailand 3",
          images: [
            "https://i.imgur.com/ZrGnFGG.jpg",
            "https://i.imgur.com/cE8R2FX.jpg",
            "https://i.imgur.com/BZIlXAF.jpg"
          ]
        }
      ]
    });

    return { state };
  }
};
</script>
